html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  background-color: var(--seashell-color);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.container-fluid {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-fluid-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 20px;
}

.btn {
  @include inline-flex-v-center;

  padding: 26px 46px;
  border-radius: 90px;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.2;
  color: #fff;
  background-color: var(--ocean-green-color);
  transition: background-color 0.3s ease-in-out;

  &__enter {
    font-size: 16px;

    &-svg {
      margin-right: 6px;
      width: 15px;
      height: 16px;
      fill: var(--light-color);
    }
  }

  &:focus {
    outline: none;
  }

  @include hover {
    background-color: var(--havelock-blue-color);
  }

  &:active {
    background-color: var(--east-bay-color);
  }
}

.btn.focus-visible {
  outline: 1px solid var(--east-bay-color);
  outline-offset: 3px;
}

.frame {
  padding: 12px 20px;
  border-radius: 90px;
  font-weight: 600;
  font-size: 12px;
  line-height: 1.2;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: var(--seashell-color);
  background-color: var(--ocean-two-color);
}

.title {
  margin: 0;
  font-weight: 500;
  font-size: 56px;
  line-height: 1.2;
  color: var(--text-color);
}

.text {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
  color: var(--text-color);
}
