.benefits {
  margin-bottom: 36px;

  &__list {
    gap: 20px;
  }

  &__item {
    padding-top: 50px;
    padding-left: 50px;
    border-radius: 20px;

    &-conveniently {
      position: relative;
      padding: 70px 50px;
      grid-column: 12 span;
      background-color: var(--light-color);
    }

    &-center {
      grid-column: 4 span;
    }

    &-available {
      background-color: var(--surf-crest-color);
    }

    &-effectively {
      background-color: var(--east-bay-color);
    }

    &-advantageous {
      background-color: var(--havelock-blue-color);
    }

    &-safely {
      position: relative;
      padding-left: 0;
      padding-top: 194px;
      padding-bottom: 174px;
      grid-column: 7/5 span;
    }
  }

  &__frame {
    display: inline-block;
    margin-bottom: 22px;

    &-advantageous {
      color: var(--text-color);
      background-color: var(--seashell-color);
    }
  }

  &__title {
    margin-bottom: 36px;
    font-size: 36px;

    &-conveniently {
      max-width: 522px;
    }

    &-center {
      font-size: 30px;
      color: var(--seashell-color);
    }

    &-available {
      max-width: 313px;
      color: var(--text-color);
    }

    &-safely {
      max-width: 420px;
      font-size: 40px;
    }

    & span {
      font-weight: 700;
    }
  }

  &__text {
    max-width: 421px;
    font-size: 20px;

    &-center {
      font-size: 18px;
      color: var(--seashell-color);
    }

    &-available {
      max-width: 313px;
      color: var(--text-color);
    }

    &-advantageous {
      max-width: 300px;
    }

    &-safely {
      max-width: 450px;
      font-size: 24px;
    }
  }

  &__image {
    &-conveniently {
      position: absolute;
      top: -45px;
      right: 81px;
    }

    &-safely {
      position: absolute;
      top: 110px;
      right: 450px;
    }
  }
}
